import React from "react";

import "./Buttons.css";

const Buttons = () => {
  return (
    <div className="buttons">
      <button className="buttons__item active"> -</button>
      <button className="buttons__item"></button>
      <button className="buttons__item">  PHS</button>
      <button className="buttons__item">  </button>
      <button className="buttons__item"></button>
      <button className="buttons__item"></button>
      <button className="buttons__item"> </button>
      <button className="buttons__item"></button>
    </div>
  );
};

export default Buttons;
