import React from 'react'
import Item from './Item'

const List = () => {
  return (
    <ul className='psychomatrix__list'>
        <Item name='' subname={' 1-2-3'}  num={4}/>
        <Item name=' ' subname={' 4-5-6'}  num={2}/>
        <Item name=' ' subname={' 7-8-9'}  num={4}/>
        <Item name='' subname={' 1-4-7'}  num={4}/>
        <Item name='' subname={' 2-5-8'}  num={2}/>
        <Item name='' subname={' 3-6-9'}  num={4}/>
        <Item name=' ' subname={' 1-5-9'}  num={5}/>
        <Item name='' subname={' 3-5-7'}  num={1}/>
    </ul>
  )
}

export default List