import React from 'react'
import { Link } from 'react-router-dom'

const Navigation = () => {
  return (
    <nav className='footer__nav'>
        <Link className='footer__link' to='#'></Link>
        <Link className='footer__link' to='#'> </Link>
        <Link className='footer__link' to='#'> </Link>
        <Link className='footer__link' to='#'></Link>
        <Link className='footer__link' to='#'></Link>
    </nav>
  )
}

export default Navigation